.container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  border: 2px solid black;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
}

.search,
button {
  font-size: 2rem;
  border: none;
  padding: 10px;
  background-color: transparent;
}

.search:focus {
  outline: none;
}

.button {
  background-color: lightgreen;
  border-radius: 0 10px 10px 0;
  border-left: 1px solid black;
}
