.body {
  height: 80vh;
  width: 80%;
  margin: auto;
  border: 5px double black;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
}

.body h2::first-letter {
  text-transform: uppercase;
}

.image {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

.image img {
  height: 70%;
}

.details {
  width: 50%;
  border: 3px solid black;
  border-radius: 10px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.75rem;
}

.button {
  border: 2px solid black;
  border-radius: 5px;
  margin: 5px;
  background-color: rgba(255, 0, 0, 0.5);
}
