.box {
  border: 1px solid black;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 150px;
  margin: 20px 75px;
}

.box h2::first-letter {
  text-transform: uppercase;
}
.box img {
  height: 100px;
  width: 100px;
}
